@charset "UTF-8";
.App {
  position: fixed;
  display: flex;
  align-items: stretch;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: white; }
  .App .calendar-container {
    flex: 1;
    overflow-y: auto;
    display: flex;
    justify-content: center; }
  .App .capture-container {
    position: absolute;
    top: 0;
    left: 100vw;
    background-color: white; }
    .App .capture-container .fake-calendar {
      width: 720px; }
  .App .sidebar {
    background-color: #EEEEEE;
    display: flex;
    flex-direction: column;
    align-items: stretch;
    width: 256px;
    border-left: 1px solid #BBBBBB; }
    .App .sidebar .header {
      position: relative;
      background-color: #DDDDDD;
      display: flex;
      align-items: center;
      border-bottom: 1px solid #BBBBBB; }
      .App .sidebar .header .primary {
