        flex: 1;
        display: flex;
        align-items: center;
        padding: 0;
        background: none; }
        .App .sidebar .header .primary select {
          align-self: stretch;
          flex: 1;
          padding: 8px 24px 8px 8px;
          font-size: 1.2em; }
        .App .sidebar .header .primary:after {
          position: absolute;
          right: 8px;
          content: '▼';
          font-size: 1em; }
      .App .sidebar .header .secondary {
