      overflow-y: auto;
      display: flex;
      flex-direction: column;
      align-items: stretch; }
    .App .sidebar .footer {
      display: flex;
      flex-direction: column;
      align-items: stretch; }
      .App .sidebar .footer > * {
        border-top: 1px solid #BBBBBB; }
  .App .sidebar-courses .scroller {
    flex-shrink: 0;
    display: flex;
    flex-direction: column;
    align-items: stretch; }
    .App .sidebar-courses .scroller > * {
      border-bottom: 1px solid #BBBBBB; }
    .App .sidebar-courses .scroller .course-list {
      display: flex;
      flex-direction: column; }
  .App .sidebar-combinations .header .sorting-option {
    position: relative;
    display: flex;
    align-items: center;
    padding: 0; }
    .App .sidebar-combinations .header .sorting-option select {
      align-self: stretch;
      flex: 1;
      padding: 8px 32px 8px 8px; }
    .App .sidebar-combinations .header .sorting-option:after {
      position: absolute;
      right: 8px;
      content: '▼'; }
  .App .sidebar-combinations .scroller {
    margin-bottom: -1px; }
    .App .sidebar-combinations .scroller .combination {
      display: flex;
      border-bottom: 1px solid #BBBBBB;
      height: 100px;
      align-items: stretch; }
      .App .sidebar-combinations .scroller .combination .number {
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: #BBBBBB;
        width: 2.4em;
        color: white;
        font-size: .8em;
        font-weight: bold; }
      .App .sidebar-combinations .scroller .combination .calendar-preview {
        flex: 1;
        cursor: pointer; }
  .App .sidebar-combinations .footer {
    position: relative; }
  .App .github-fork-ribbon:before {
    background-color: #666666; }
  .App.mobile {
    flex-direction: column-reverse; }
    .App.mobile .tab-container {
      flex-shrink: 0;
      display: flex;
      border-bottom: none; }
      .App.mobile .tab-container .tab {
        flex: 1;
        padding: 16px;
        display: flex;
        justify-content: center;
        align-content: center;
        border-right: 1px solid #BBBBBB;
