          color: black;
          border-top: 1px solid #BBBBBB;
          border-bottom: none; }
        .App.mobile .tab-container .tab:last-child {
          border-right: none; }
    .App.mobile .sidebar {
      flex: 1;
      width: auto;
      border-left: none; }
    .App.mobile .github-fork-ribbon {
      display: none; }
